

import tools from '../../lib/tools.js';
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tabBar from '../../components/TabBar.vue';
import BScroll from 'better-scroll';
export default {
name:'findDoctors',
components:{Flexbox, FlexboxItem, tabBar,Toast},
data () {
return {
id:'',
searchValue:'',
prompt:true,
showToast:false,
toastMsg:'',
doctorList:[],
typeId:1,   // 1:医生   2：机构
navType:0,
pageNumber:1,
pageSum:0,
times: 0,
loading: false,
loadable:false,
pullUpObj:{
type:0,
text:'松开立即加载'
},// 0 初始化 1可以加载 2最后一页
showTabBar:true
}
},
mounted () {
this.init();
},
methods:{
inputClick(){
this.prompt = false;
document.getElementById('searchInput').focus();
this.showTabBar = false;
},
onSubmit(e){
let theEvent = e || window.event;
this.searchValue += '';



theEvent.preventDefault();
this.pullUpObj = {
type:0,
text:'松开立即加载'
};
this.showTabBar = true;
this.search();
return false;
},
blurFn(){



this.prompt = true;
this.searchValue = '';
this.showTabBar = true;

},
selectNavType(type){
this.navType = type;
this.reLoad();
},
init(){
var vm = this;
console.log('init');


let body = {
pageSize:10,
pageNumber:this.pageNumber,
userStatus:1,
shieldJY:1,
deptId:this.navType==0?'':this.navType,
roleId:this.typeId
}
this.searchValue && (body.name = this.searchValue);
this.$http.post('/backstage/query',body).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data.list && data.data.list.length==0){
vm.showToast = true;
vm.toastMsg = '未查询到对应的数据';
if(vm.pullUpObj.type == 0){
vm.doctorList = [];
}
return ;
}





this.pageSum = data.data.pageSum;
this.loading = false;
this.loadable = (this.pageSum>1);
if(this.pullUpObj.type==0){
vm.doctorList = data.data.list;
vm.pullUpObj.type = 1;
}else{
vm.doctorList = [...vm.doctorList,...data.data.list];
}
}else{
this.loading = false;
this.loadable = false;
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
this.loading = false;
this.loadable = false;
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
nextPage(){
console.log(this.pageSum,this.times);
if(this.pageSum!=0 && !(this.pageSum==this.times)){
this.times += 1;
this.pageNumber += 1;
console.log('nextPage');
this.init();
}else{
this.pullUpObj.type = 2;
this.pullUpObj.text = '已经到底了'
}
},
reLoad(){
this.pullUpObj = {
type:0,
text:'松开立即加载'
};
this.times = 0;
this.pageSum = 0;
this.pageNumber = 1;
this.init();
},
search(){
this.pageNumber = 1;
this.pageSum = 0;
this.times = 0;
this.init();
},
searchType(type){
this.typeId = type;
this.reLoad();
},
goToIndex(item){
let url = '';
if(item.roleId==1){
url = '/m/doctorIndex.html?md=';
} else {
url = '/m/orgIndex.html?md='
}
tools.next(url+item.managerId);
}
}
}



